(function() {
  'use strict';

  angular
    .module('palazzoLuce')
    .run(runBlock);

  /** @ngInject */
  function runBlock(uiState, $rootScope, defaultPageTitle, defaultPageDescription, defaultPageImages, $location) {
    lightbox.option({ // eslint-disable-line
      'albumLabel': '%1 / %2'
    })

    $rootScope.uiState = uiState;

    // Scroll to top and update searchBar visibility on state change
    var stateChangeSuccess = $rootScope.$on('$stateChangeSuccess', function(event, toState){
      angular.element('body').scrollTop(0);
      var pms = toState.params || {};
      uiState.pageTitle = pms.pageTitle || defaultPageTitle;
      uiState.pageDescription = pms.pageDescription || defaultPageDescription;
      uiState.pageUrl = pms.pageUrl || $location.absUrl();
      uiState.pageImages = pms.pageImages || defaultPageImages;

    });

    // Destroy watchers
    $rootScope.$on('$destroy', function() {
      stateChangeSuccess();
    });
  }

})();

(function() {
  'use strict';

  angular
    .module('palazzoLuce')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider, defaultPageTitle) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main',
        params:{
          pageTitle: defaultPageTitle,
          pageDescription: 'Palazzo della Luce, un capolavoro di eclettismo, nel cuore di Torino, in via Bertola. Il grande salone è il luogo ideale per dare nuova luce ai tuoi eventi.'
        }
      })
      .state('history', {
        url: '/la-storia',
        templateUrl: 'app/history/history.html',
        controller: 'HistoryController',
        controllerAs: 'history',
        params:{
          pageTitle: 'La Storia - ' + defaultPageTitle,
          pageDescription: 'Palazzo della Luce da oltre un secolo è simbolo di tradizione e innovazione ed ha ospitato tra le sue mura grandi aziende come Sip, Radio Torino ed Enel'
        }
      })
      .state('architecture', {
        url: '/architettura',
        templateUrl: 'app/architecture/architecture.html',
        params:{
          pageTitle: 'Architettura - ' + defaultPageTitle,
          pageDescription: 'Palazzo della luce è definito "la piccola Madama", con il suo stile eclettico si ispira alla maestosa facciata di Palazzo Madama in Piazza Castello a Torino.'
        }
      })
      .state('location', {
        url: '/location',
        controller: 'LocationController',
        controllerAs: 'vm',
        templateUrl: 'app/location/location.html',
        params:{
          pageTitle: 'Location per eventi a Torino - ' + defaultPageTitle,
          pageDescription: 'Una elegante location a Torino per eventi pubblici e privati: convegni, sfilate, esposizioni, matrimoni, set fotografici, catering, cene di gala, ricevimenti.'
        }
      })
      .state('services', {
        url: '/servizi',
        templateUrl: 'app/services/services.html',
        params:{
          pageTitle: 'Servizi per eventi a Torino - ' + defaultPageTitle,
          pageDescription: 'Tanti servizi per i tuoi eventi e i tuoi catering: maxi schermo, regia, cucina professionale, impianto audio-video-luci, climatizzazione, accesso disabili.'
        }
      })
      .state('contacts', {
        url: '/contatti',
        templateUrl: 'app/contacts/contacts.html',
        params:{
          pageTitle: 'Servizi per eventi a Torino - ' + defaultPageTitle,
          pageDescription: 'Nel cuore di Torino, specialisti nell’organizzazione di eventi: convegni, sfilate, esposizioni, matrimoni, cene di gala, avvenimenti pubblici e privati.'
        }
      });

    $urlRouterProvider.otherwise('/');
  }

})();

